* {
	box-sizing: border-box;
}

*::before,
*::after {
	box-sizing: border-box;
}

* {
	margin: 0;
	padding: 0;
}

ul,
ol {
	list-style: none;
}

a {
	color: inherit;
	text-decoration: none;
}

img {
	max-width: 100%;
}

body {
	background-color: #eee;
}

button {
	cursor: pointer;
	outline: none;
}

input {
	outline: none;
}

.globalHeader {
	margin: 20px;
}

.globalHeader > .se {
	display: flex;
	justify-content: flex-start;
}
.globalHeader > .se > button {
	font-size: 16px;
	border: none;
	background: none;
	margin-left: 20px;
}
.globalHeader > .se > button:first-child {
	margin-left: 5px;
}
.globalHeader > .se > .active {
	color: #0382b9;
	font-weight: bold;
	position: relative;
}
.globalHeader > .se > .active::after {
	content: '';
	display: block;
	position: absolute;
	width: 100%;
	height: 2px;
	left: 0;
	bottom: 0;
	background: #0382b9;
}

@media (min-width: 500px) {
	.globalHeader {
		margin: 60px 0 100px;
	}
}

.searchForm {
	display: flex;
	justify-content: space-between;
}

@media (min-width: 500px) {
	.searchForm {
		max-width: 400px;
		margin-left: auto;
		margin-right: auto;
	}
	.globalHeader > .se {
		max-width: 400px;
		margin: 10px auto;
	}
}

.searchForm > input {
	width: 100%;
	margin-right: 10px;
	height: 40px;
	padding: 0 10px;
	border: 1px solid #ddd;
	border-radius: 10px;
}

.searchForm > button {
	white-space: nowrap;
	padding: 0 27px;
	border: 1px solid #ddd;
	border-radius: 10px;
	background: #0282b9;
	color: white;
	font-size: 16px;
}

.globalMain {
	max-width: 900px;
	margin-left: auto;
	margin-right: auto;
}

.siteList {
	display: flex;
	flex-wrap: wrap;
	margin: 20px;
	justify-content: space-between;
}

@media (min-width: 500px) {
	.siteList {
		margin-left: 0;
		margin-right: -25px;
		justify-content: flex-start;
	}
}

.siteList > li {
	margin-bottom: 10px;
}

@media (min-width: 500px) {
	.siteList > li {
		margin-right: 25px;
	}
}

.siteList .site {
	width: 160px;
	height: 100px;
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
	background-color: white;
	margin-bottom: 10px;
	border-radius: 20px;
	padding: 20px 0;
	position: relative;
	cursor: pointer;
}

.siteList .site > .close {
	position: absolute;
	right: 8px;
	top: 5px;
	display: none;
	cursor: default;
}

.siteList .site:hover > .close {
	display: block;
}

.siteList .site > .logo {
	width: 64px;
	height: 64px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 64px;
	text-transform: uppercase;
}

.siteList .site > .link {
	font-size: 14px;
	margin-top: 4px;
}

.siteList .addButton {
	width: 160px;
	display: flex;
	justify-self: center;
	align-items: center;
	flex-direction: column;
	background-color: white;
	margin-bottom: 10px;
	border-radius: 20px;
	padding: 5px 0;
}

.siteList .addButton .icon {
	width: 56px;
	height: 56px;
}

.siteList .addButton .text {
	font-size: 14px;
	margin-top: 4px;
}

.siteList .addButton .icon-Wrapper {
	width: 64px;
	height: 64px;
	display: flex;
	justify-self: center;
	align-items: center;
}
